#frontPageAbout{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 48px;
    color: white;
    text-align: center;
    font-weight: bold;
}
#frontButton{
    font-size: 24px;
    max-width: 200px;
    min-height: 50px;
    border: 2px solid white;
    padding: 15px;
    transition: 0.3s ease-in-out;
    animation: createBox .3s;
}
@keyframes createBox {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}
#frontButton:hover{
    background-color: #04c2c9;
    border-color: #04c2c9;
}
#myName{
    color: #e31b6d;
}

#myImage{
 width: 100%;
}
#textAbout{
    padding-top: 60px;
    color:#444649;
    font-weight: 600;
    text-align: center;
}
#textInfo{
    font-size: 20px;
    padding-top: 25%;
    padding-bottom: 50px;
}
/* media queries */
@media only screen and (max-width : 600px){
    #frontPageAbout{
        font-size:24px
    }
    #frontButton{
        font-size: 16px;
    }
    #textInfo{
        padding-top: 0;
        font-size: 16px;
    }
}
@media only screen and (min-width:600px) and (max-width : 990px){
    #textInfo{
        padding-top: 0;
    }
} 